header {
    min-height: 840px;
    background-image: url(../style/images/Rectangle.png);
    @include flex(initial, center);
    @include bg; }
.inline-header {
    min-height: 100px;
    @include flex(center, space-between); }
.logo {
    min-height: 100px;
    @include flex(center, center);
    flex-direction: column; }
.logo p {
    @include text-settings(12px, 400, white, $rob); }
.navigation-container {
    @include flex(center, space-between); }
.navigation-container a {
    @include text-settings(14px, 400, white, $rob);
    @include flex(initial, center); }
header h1 {
    max-width: 556px;
    @include text-settings(48px, 700, $yellow, $pd);
    text-align: center;
    line-height: 64px; }
.title-container {
    min-height: 600px;
    @include flex(center, center);
    flex-direction: column; }
.mouse {
    display: block;
    width: 40px;
    height: 40px;
    background-image: url(../style/images/mouse.png);
    @include bg;
    cursor: pointer; }
.contacts-container {
    @include flex(center, space-around);
    margin-top: 50px; }
.contacts-container span {
    display: block;
    width: 21px;
    height: 21px;
    @include bg;
    margin-right: 10px; }
.phone span {
    background-image: url(../style/images/Vector.png); }
.address span {
    background-image: url(../style/images/Group.png); }
.phone, .address {
    @include text-settings(18px, 400, white, $rale);
    @include flex(center, center);
    width: 30%;
    cursor: pointer; }
@media screen and (max-width: 1022px) {
    .navigation-container {
        @include flex(center, initial);
        width: 70%; }
    .inline-header {
        padding: 10px; } }
@media screen and (max-width: 566px) {
    .inline-header {
        flex-direction: column; }
    .navigation-container {
        width: 100%; }
    .phone, .address {
        width: 50%; } }
